import Carousel from "react-bootstrap/Carousel";
import slides from "assets/slider";
import css from "./Slider.module.css";

const Slider = () => {
  return (
    <Carousel
      className={css.slider}
      controls={false}
      interval={5000}
    >
      {slides.map((s, i) => (
        <Carousel.Item style={{ height: "100%" }} key={i}>
          <img
            className={`${css.slide} d-block w-100`}
            src={s}
            alt="Third slide"
          />
          <Carousel.Caption>
            {i === 0 ?
              <><span>Une élégante résidence,</span><br /><span>réservée à 32 privilégiés</span></> :
              <><span>Votre nouvel art de vivre</span><br /><span>à Villeneuve-Loubet</span></>
            }
          </Carousel.Caption>
        </Carousel.Item>
      ))}
    </Carousel>
  );
};

export default Slider;
