import React, { useEffect, useState } from "react";
import api from "api";
import useLocalStorage from "api/useLocalStorage";

export const TokenContext = React.createContext();

export const TokenProvider = ({ children }) => {
  const [localToken, setLocalToken] = useLocalStorage("token", null);
  const [localExpiration, setLocalExpiration] = useLocalStorage(
    "expiration",
    null
  );
  const [token, setToken] = useState(localToken);

  // Fetching options : TODO : in .env file
  const options = {
    method: "POST",
    data: {
      email: process.env.REACT_APP_USER,
      password: process.env.REACT_APP_PASSWORD,
    },
  };

  // Copy LocalStorage in State
  useEffect(() => {
    setToken(localToken);
  }, [localToken]);

  // Fetch Api Key
  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await api.request("/key/", options);
        return res.data;
      } catch (error) {
        console.error(error);
      }
    };
    // If token is null or expired in localstorage, go fetch one
    if (
      !localToken ||
      !localExpiration ||
      new Date() >= new Date(localExpiration)
    ) {
      fetchData()
        .then((data) => {
          localStorage.clear();
          setLocalToken(data.token);
          setLocalExpiration(data.expiration);
        })
        .catch((error) => console.error(error));
    }
  }, []);

  return (
    <TokenContext.Provider value={token}>{children}</TokenContext.Provider>
  );
};
